import { Typography } from "@mui/material";
import { ContentContainer } from "../../view/Home.styles";

const HEADER_TEXT =
  "Witaj nasz szanowny pracowniku! Przygotuj się do swojej zmiany chcemy abyś czuł się dobrze w swoim zawodzie <3 Przypominamy by sprawdzać czy samochody są stabilne na platformie.";

const Header = () => {
  return (
    <ContentContainer>
      <Typography variant="h5">{HEADER_TEXT}</Typography>
    </ContentContainer>
  );
};

export default Header;
